import * as React from 'react'
import TopBar from '../Topbar'
import { Alert, Box, Button } from '@mui/material';
import "../App.css"
import { red, yellow, green } from '@mui/material/colors';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Unstable_Grid2';
import { styled } from '@mui/material/styles';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import { Link } from 'react-router-dom';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import dayjs from 'dayjs';
const { v4: uuidv4 } = require('uuid');

export function ErrorPage({ errorId, errorSummery, data, user }) {
    const [countdown, setCountdown] = React.useState(30)
    const [block, setBlock] = React.useState([0, 1, 2])

    const errorIdList = [0, 403, 404, 500, 700, 701, 702, 1000]
    const errorDefSummery = [
        ["", ": )"],//0
        ["", ""],//403
        ["", ""],//404
        ["", ""],//500
        ["", ""],//700
        ["", ""],//701
        ["", ""],//702
        ["", ""],//1000
    ]

    const [pageContent, setPageContent] = React.useState(["", ""])

    const [randomCode, setRandomCode] = React.useState(uuidv4().slice(0, 4))
    const [reportState, setReportState] = React.useState("...")

    const [errorTime, setErrorTime] = React.useState(dayjs().format("YYYY/MM/DD HH:mm:ss"))


    function sendReport() {
        setReportState("...")
        console.log("error page", errorId)
        fetch("/api/report/pusherrorlog", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                randomCode: randomCode,
                errorCode: errorId,
                time: errorTime,
                path: window.location.pathname + window.location.search
            }),
        })
            .then(res => res.json())
            .then(res => {
                if (res.ok) {
                    setReportState("")
                } else {
                    setReportState(<><Button variant='text' onClick={() => sendReport()}></Button></>)
                }
            })
    }

    function delay(n) {
        return new Promise(function (resolve) {
            setTimeout(resolve, n * 1000);
        });
    }
    React.useEffect(() => {

        async function wait() {
            if (errorId == 500 || errorId >= 700 && errorId < 800) {
                setCountdown(60)

                for (let i = 0; i < 60; i++) {
                    await delay(1)
                    setCountdown((old) => old - 1)
                }

            } else {
                setCountdown(0)
            }
        }
        wait()
    }, []);




    React.useEffect(() => {
        sendReport()
    }, [])
    return (
        <>
            <TopBar needCheckLogin={false} loggedIn={true} data={data.data} user={user} title={""} />
            <Box sx={{ p: 3 }}>

                <Typography variant="h2" gutterBottom>
                    : (
                </Typography>
                <h2>
                    {errorDefSummery[errorIdList.indexOf(errorId)][0]}
                </h2>
                <p>
                    {errorDefSummery[errorIdList.indexOf(errorId)][1]}
                </p>

                <p>
                    <Button sx={{ mr: 1 }} variant="contained" onClick={() => window.location.reload()} disabled={countdown > 0}>{countdown > 0 ? `${countdown}` : ""}</Button>
                    <Button color="secondary" variant="outlined" onClick={() => window.location.href = "/"}></Button>
                    <br />{errorId == 500 || errorId >= 700 && errorId < 800 ? <Alert sx={{ mt: 1 }} severity="error"></Alert> : <></>}
                </p>

                <hr />
                <code>
                    : {errorId}
                    <p></p>
                    :
                    <pre>
                        path:{window.location.pathname}{window.location.search}<br />
                        user:{data.data.userid}<br />
                        time:{errorTime}<br />
                        uuid:{randomCode}</pre>
                    {reportState}
                </code>

            </Box>
        </>
    )
}
