import * as React from 'react'
import TopBar from '../Topbar'
import { Box, Button } from '@mui/material';
import "../App.css"
import { red, yellow, green } from '@mui/material/colors';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Unstable_Grid2';
import { styled } from '@mui/material/styles';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import { Link } from 'react-router-dom';
import { InputForm } from '../inputBoxs';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';


export function TeacherScore({ data, user, handleError }) {
  const [students, setStudents] = React.useState([
    { username: "" }
  ])


  const [scoreData, setScoreData] = React.useState([
    { id: 1, [UrlParam("q")]: "null" }
  ])
  const [tbody, setTbody] = React.useState(<>Loading</>)


  const [dialogSubmitBtnText, setDialogSubmitBtnText] = React.useState(<></>)

  const [scoreSetting, setScoreSetting] = React.useState({ subject: "", scoreName: "", summery: "", })

  const [newScore, setNewScore] = React.useState()
  const [newPrivateMsg, setNewPrivateMsg] = React.useState()

  const [newTitle, setNewTitle] = React.useState()
  const [newAnnousment, setNewAnnousment] = React.useState()
  const [newTags, setNewTags] = React.useState()
  const [dialogSubmitBtnText2, setDialogSubmitBtnText2] = React.useState(<></>)

  function UrlParam(name) {
    var url = new URL(window.location.href),
      result = url.searchParams.get(name);
    return result
  }


  const [open, setOpen] = React.useState(false);
  const [openingId, setOpeningId] = React.useState(
    {
      "scoreData": {
        "id": 1,
        "stdId": "s111253",
        [UrlParam("q")]: "0%|%null"
      },
      "userData": {
        "id": 1,
        "username": "",
        "userid": "",
      }
    }
  )

  const [open2, setOpen2] = React.useState(false);
  const [openingId2, setOpeningId2] = React.useState(
    {
      id: UrlParam("q"),
      title: "",
      annousment: "",
      tag: ""
    }
  )

  const [open3, setOpen3] = React.useState(false);

  const handleClickOpen = (n) => {

    setOpen(true);
    setOpeningId(n)
    //////////////////////
    setNewScore(n.scoreData[UrlParam("q")].split("%|%")[0])
    setNewPrivateMsg(n.scoreData[UrlParam("q")].split("%|%")[1] !== 'null' && n.scoreData[UrlParam("q")].split("%|%")[1] !== "" ? n.scoreData[UrlParam("q")].split("%|%")[1] : "")
  };

  const handleClickOpen2 = (n) => {

    setOpen2(true);
    setOpeningId2(n)
    //////////////////////


    setNewTitle(scoreSetting.scoreName)
    setNewAnnousment(scoreSetting.summery !== "undefined" && scoreSetting.summery ? scoreSetting.summery : "")
    setNewTags(scoreSetting.subject.split(","))
  };

  const handleClose = (n) => {

    setDialogSubmitBtnText(<><CircularProgress size="1rem" /> </>)
    if (n === "update") {
      fetch('/api/updatescore', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(
          { id: (openingId.userData.id), scoreid: UrlParam("q"), scoreData: (newScore ? newScore : null) + "%|%" + (newPrivateMsg == "" ? null : newPrivateMsg) }),
      })
        .then(res => res.json())
        .then(
          (res) => {
            if (res.ok) {
              setDialogSubmitBtnText("")
              setOpen(false)
              getAllStdScore()

              setDialogSubmitBtnText("")
            } else {
              getAllStdScore()

              setDialogSubmitBtnText("")
            }


          }
        ).catch((e) => {
          getAllStdScore()

          getAllStdScore()
          setDialogSubmitBtnText("")
        })

    } else {
      setOpen(false)
      setDialogSubmitBtnText("")
    }
  };

  const handleClose2 = (n) => {

    setDialogSubmitBtnText2(<><CircularProgress size="1rem" /> </>)
    if (n === "update") {
      fetch('/api/updatescoresetting', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(
          {
            scoreid: UrlParam("q"),
            title: newTitle,
            tags: newTags,
            annousment: newAnnousment

          }),
      })
        .then(res => res.json())
        .then(
          (res) => {
            if (res.ok) {
              setDialogSubmitBtnText2("")
              setOpen2(false)
              getAllStdScore()
              setDialogSubmitBtnText2("")
            } else {
              getAllStdScore()
              setDialogSubmitBtnText2("")
            }

          }
        ).catch((e) => {
          getAllStdScore()
          setDialogSubmitBtnText2("")
        })

    } else {
      setOpen2(false)
      setDialogSubmitBtnText2("")
    }
  };

  function deleteScore() {
    if (window.confirm("?\n!!")) {
      fetch('/api/deletescore', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(
          {
            scoreid: UrlParam("q"),
          }),
      }).then(res => res.json())
        .then((res) => {
          if (res.ok) {
            alert("")
            window.location.href = "/"
          } else {
            window.alert("")
          }

        })
        .catch(() => {
          window.alert("")
        })
    }
  }


  function getAllStdScore() {
    fetch("/api/getallstudentscorebyid", {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        uid: UrlParam("q")
      }),
    })
      .then(res => res.json())
      .then(res => {
        if (res.ok) {

          setScoreData(res.data.result)
          fetch("/api/getallstudentsforscore", {
            method: 'GET',
            headers: {
              'Content-Type': 'application/json',
            },

          })
            .then(res => res.json())
            .then(res => {
              if (res.ok) {

                var list = []
                for (let i = 0; i < res.data.result.length; i++) {
                  if (res.data.result[i].userid.includes("s")) {

                    var object = res.data.result[i]
                    list.push(object)

                  }
                }

                setStudents(list)
              } else {
                handleError([true, 700])
                alert("!!")
              }
            })
        } else {
          handleError([true, 700])
          alert("!!")
        }


      })

    fetch("/api/getscoremap", {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ id: UrlParam("q") }),
    })
      .then(res => res.json())
      .then(res => {
        if (res.ok) {

          var list = [], k = false
          for (let i = 0; i < res.data.result.length; i++) {
            list.push(res.data.result[i].uid)

            if (res.data.result[i].uid == UrlParam("q")) {
              k = true
              setScoreSetting(res.data.result[i])

            }
          }
          if (!k) alert("")
        } else {
          handleError([true, 700])
          alert("!!")
        }

      })
  }

  React.useEffect(() => {
    getAllStdScore()
  }, [])

  React.useEffect(() => {
    try {
      if (students.length > 1 && scoreData.length > 1 && students.length == scoreData.length) {
        setTbody(
          <>
            <TableBody>
              {scoreData.map((row, i) => (
                <TableRow
                  key={row.id}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                  <TableCell>{row.id}</TableCell>
                  <TableCell>{students[i].username}</TableCell>
                  <TableCell>{row[UrlParam("q")].split("%|%")[0] == "null" || row[UrlParam("q")].split("%|%")[0] == "undefined" ? "" : row[UrlParam("q")].split("%|%")[0]}</TableCell>
                  <TableCell>{row[UrlParam("q")].split("%|%")[1] == "null" || row[UrlParam("q")].split("%|%")[1] == "undefined" ? "()" : row[UrlParam("q")].split("%|%")[1]}</TableCell>
                  <TableCell><Button variant="contained" onClick={() => handleClickOpen({ scoreData: row, userData: students[i] })}></Button></TableCell>
                </TableRow>
              ))}
            </TableBody>
          </>
        )
      }
    } catch (e) {
      console.warn(e)
      setTbody(<><br /></>)

    }

  }, [students, scoreData])



  return (
    <>

      <TopBar needCheckLogin={true} loggedIn={true} data={data.data} user={user} title={""} />
      <Box sx={{ p: 3 }}>


        <Card sx={{ minWidth: 275 }}>
          <CardContent>
            <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              
            </Typography>
            <Typography variant="h5" component="div">
              {scoreSetting.scoreName}
            </Typography>
            <Typography variant="body2">
              <p>{scoreSetting.summery !== "undefined" && scoreSetting.summery ? scoreSetting.summery : "()"}</p>
              <p>:<br />
                <Button onClick={() => setOpen3(true)}></Button>
              </p>
              <span>id: {UrlParam("q")}</span>
            </Typography>
          </CardContent>
          <CardActions>
            <Button size="small" onClick={handleClickOpen2}></Button>
          </CardActions>
        </Card>

        <p></p>

        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
              <TableRow>

                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
              </TableRow>
            </TableHead>
            {tbody}
          </Table>
        </TableContainer>
        <p></p>
        <Button onClick={() => window.location.reload()}></Button>
        &nbsp;
        <Button color='error' variant='contained' onClick={() => deleteScore()}></Button>
      </Box>


      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {" " + openingId.userData.username + " "}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            :{openingId.scoreData[UrlParam("q")].split("%|%")[0] && openingId.scoreData[UrlParam("q")].split("%|%")[0] !== 'null' ? openingId.scoreData[UrlParam("q")].split("%|%")[0] : ""}<br />
            <p></p>
            <TextField type='number' variant="standard" label="" value={newScore} onInput={(e) => setNewScore(e.target.value)} />
            <p></p>
            <TextField type='text' variant="standard" label="" value={newPrivateMsg} onInput={(e) => setNewPrivateMsg(e.target.value)} />

          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}></Button>
          <Button onClick={() => handleClose("update")}>
            {dialogSubmitBtnText}
          </Button>
        </DialogActions>
      </Dialog>





      <Dialog
        open={open2}
        onClose={handleClose2}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title2">
          {""}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description2">

            <TextField type='text' variant="standard" label="" value={newTitle} onInput={(e) => setNewTitle(e.target.value)} />
            <p></p>
            <TextField
              multiline
              rows={2}
              type='text' variant="standard" label="" value={newAnnousment} onInput={(e) => setNewAnnousment(e.target.value)} />
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose2}></Button>
          <Button onClick={() => handleClose2("update")}>
            {dialogSubmitBtnText2}
          </Button>
        </DialogActions>
      </Dialog>


      <Dialog
        open={open3}
        onClose={() => setOpen3(false)}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title2">
          {""}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description2">
            <p></p>
            <p>:<br />
              <TextField type='text'
                InputProps={{
                  readOnly: true,
                }} sx={{ width: "100%" }} variant="standard" value={`https://h210-score-production.up.railway.app/score/?q=${UrlParam("q")}`} />
            </p>
            <p>:<br />
              <TextField
                multiline
                rows={6}
                InputProps={{
                  readOnly: true,
                }}
                type='text' sx={{ width: "100%" }} variant="standard" value={`${scoreSetting.scoreName} :\nhttps://h210-score-production.up.railway.app/score/?q=${UrlParam("q")}\nP\n**`} />
            </p>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpen3(false)}></Button>
        </DialogActions>
      </Dialog>
    </>
  )
}
