import * as React from 'react'
import TopBar from '../Topbar'
import { Alert, AlertTitle, Box, Button, TextField } from '@mui/material';
import "../App.css"
import { red, yellow, green } from '@mui/material/colors';
import Paper from '@mui/material/Paper';
import { styled } from '@mui/material/styles';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import { Link } from 'react-router-dom';
import FaceIcon from '@mui/icons-material/Face';
import Chip from '@mui/material/Chip';

export function Profile({ data, user, handleError }) {
  const [oldPassword, setOldPassword] = React.useState()
  const [newPassword, setNewPassword] = React.useState()
  const [confirmNewPassword, setConfirmNewPassword] = React.useState()


  function UrlParam(name) {
    var url = new URL(window.location.href),
      result = url.searchParams.get(name);
    return result
  }


  function resetPassword() {
    if (newPassword !== confirmNewPassword) {
      alert("")
    } else {
      if (newPassword == "" || confirmNewPassword == "" || !newPassword) {
        alert("!!")
      } else {
        fetch("/api/changepass", {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            userid: data.data.userid,
            oldpass: oldPassword,
            newpass: newPassword,
          })
        })
          .then(res => res.json())
          .then(res => {
            if (res.ok) {
              alert("")
              setOldPassword("")
              setNewPassword("")
              setConfirmNewPassword("")
              window.location.href = "/"
            }
            else {
              if (res.code == 403) {
                handleError([true, 500])
                alert("!!")
              } else if (res.code == 404) {
                alert("\n")
                window.location.reload()
              }

            }

          })
          .catch(() => {
            alert("")
          })
      }

    }
  }


  return (
    <>


      <TopBar needCheckLogin={true} loggedIn={true} data={data.data} user={user} title={""} />

      <Box sx={{ p: 3 }}>
        <Paper sx={{ p: 2 }}>
          <div>
            <h2>
              {data.data.username}<br />
              {data.data.userid}
              <br />
              {
                data.data.role === "std" ?
                  <Chip label="" color="primary" />
                  :
                  data.data.role === "par" ?
                    <Chip label="" color="secondary" />
                    :
                    <Chip label="" color="success" />
              }</h2>

          </div>
        </Paper>
        <p></p>
        <Paper sx={{ p: 2 }}>
          <h2></h2>
          <Alert severity='warning' >
            <AlertTitle></AlertTitle>
            {data.data.role !== "teacher" ? "" : ""}
          </Alert>
          <TextField type='password' label="" helperText="" variant="standard" value={oldPassword} onChange={(e) => setOldPassword(e.target.value)} />
          <p></p>
          <TextField type='password' label="" variant="standard" value={newPassword} onChange={(e) => setNewPassword(e.target.value)} />
          <br />
          <TextField type='password' label="" variant="standard" value={confirmNewPassword} onChange={(e) => setConfirmNewPassword(e.target.value)} />
          <p></p>
          <Button variant='contained' onClick={resetPassword}></Button>
        </Paper>

      </Box>
    </>
  )
}